@import "./../../configs/variables";

#FooterComponent {

  background-color: $primary-dark;
  padding: 32px $side-space;

  #languageSelect {
    font-size: 1.1rem;
    color: rgba(0, 0, 0, 0.87);
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.14);
    border-radius: .25rem;
    outline: none;
    width: 200px;
    height: 40px;
  }

  #languageSelectLabel{
    color: white;
    margin-bottom: 5px;
  }

  @media (max-width: 600px) {
    padding: 32px $side-space-600;
  }

}