@import "../../configs/variables";

#TeamComponent {

  padding: 32px $side-space 80px;
  background-color: $primary-color;
  box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

  .grid {
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

  }

  .cardContainer {
    width: 300px;
    background-color: white;
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    border-radius: 10px;
    box-sizing: border-box;
    transition: box-shadow .5s;
    padding: 16px;
    margin: 16px 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .cardContainer:hover {
    box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

  }

  .personImage {
    margin-top: 16px;
    border-radius: 110px;
    height: 130px;
    width: 130px;
    border: $primary-light 1px solid;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .personImage-ceo {
    background-image: url("../../assets/images/ceo.jpg");
  }

  .personImage-cto {
    background-image: url("../../assets/images/cto.jpeg");
  }

  .personImage-advisor {
    background-image: url("../../assets/images/randy_advisor.png");
  }

  h2 {
    margin-bottom: 0;
    color: $primary-color;
  }

  h4 {
    font-weight: 100;
    color: $secondary-color;
  }


  @media (max-width: 600px) {
    padding: 32px $side-space-600;
  }

}