@import "../../../configs/variables";
#Notification {

  span {
    display: block;
  }

  .error {
    background-color: $error-color;
  }

  .success {
    background-color: rgb(5, 156, 5);
  }

  .notification-base {

    color: white;
    padding: 16px;
    border-radius: 30px;
    font-weight: 100;
    box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    transition: 1s linear;
    margin-top: 10px;
  }



}