@import "../../configs/variables";

#StepsComponent {
  margin: 50px $side-space;

  .stepsContainer {
    display: flex;
    flex-direction: column;

    .layout:nth-child(even) {
      .stepContainer {
        margin-right: -130px;
        background-color: $primary-color;
        color: white;
      }
    }

    .layout:nth-child(odd) {
      .stepContainer {
        margin-left: -130px;
      }
    }

    .layout {
      display: flex;
      flex-direction: row;
      justify-content: center;
      color: $primary-color;
    }


  }


  .stepContainer {

    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    border-radius: 10px;
    width: 70%;
    min-width: 300px;
    padding: 16px;
    margin: 24px 0 8px;
    display: flex;
    flex-direction: row;
    background-color: white;
  }

  .stepContainer:hover {
    box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

  }

  .stepIcon {
    min-width: 100px;
    width: 100px;
    margin-right: 16px;
  }

  .stepTitle {
    //color: inherit;
  }

  .stepDescription {

  }


  @media (max-width: 600px) {
    margin: 32px $side-space-600;

    .layout:nth-child(even) {
      .stepContainer {
        flex-direction: row-reverse;
      }
    }

    .layout {
      .stepContainer {
        margin-left: 0 !important;
        margin-right: 0 !important;
      }
    }
  }

}