@import "../../configs/variables";

#SidenavCmpnt {


  $toggleSpeed: .3s;
  display: flex;
  position: fixed;
  height: 100vh;
  z-index: 15;

  #sidenavTint {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    cursor: pointer;
  }

  #sidenavContent {
    min-width: 300px;
    display: none;
    flex-direction: column;
    background-color: #f1f1f1;
    z-index: 11;
    left: 0;
    transition: transform .5s cubic-bezier(0, .52, 0, 1);
  }

  .navOpen {
    transform: translate3d(0vw, 0, 0);
  }

  .navClosed {
    transform: translate3d(-300px, 0, 0);
  }

  .sidenavButton {
    color: rgba(0, 0, 0, 0.56);
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
    height: 40px;
    font-size: 1.2em;
    border: none;
    outline: none;
    background-color: transparent;

    &:active {
      background-color: #0000000f;
    }

  }

  .selectedSidenavButton {
    border: 1px solid $primary-dark;
    background-color: $primary-light;
    color: $primary-dark;

    &:active {
      background-color: darken($primary-light, 10);
    }
  }

  .sidenavButtonIcon {
    color: inherit;
    padding-right: 32px;
    font-size: 1.4em;
  }


  #sidenavFooter {
    padding: 16px;
    font-weight: 100;
    font-size: smaller;
    display: flex;
    flex-direction: row;

    span {
      color: $primary-color;
      margin-right: 32px;
      cursor: pointer;
    }
  }

  .navButton {
    width: 100%;
  }

  @media (max-width: 800px) {
    display: flex;
    #sidenavContent {
      display: flex;
      position: absolute;
      height: 100%;
      box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12) !important;
    }

  }

  @media (max-width: 600px) {


  }

  @media (max-width: 400px) {

  }

}
