@import "./configs/variables";

html {
  height: 100vh;
}

body {
  margin: 0;
  background-color: $neutral-color;
  display: flex;
  flex-direction: column;
  height: 100%;
  font-family: 'Nunito', sans-serif;
}

p {
  font-family: 'Lato', sans-serif;
}

.flex-direction-column {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.flex-1 {
  display: flex;
  flex: 1;
}

.color-white {
  color: white;
}

.margin-0 {
  margin: 0;
}

.text-align-center {
  text-align: center;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.font-weight-light {
  font-weight: 200;
}

.color-primary {
  color: $primary-color;
}

.width-100-p {
  width: 100%;
}

.input:focus {
  border-color: $primary-color;
}

.input {
  box-sizing: border-box;
  display: block;
  flex: 1;
  margin-right: 16px;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 2;
  color: rgba(0, 0, 0, 0.87);
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.14);
  border-radius: .25rem;
  transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  outline: none;
  caret-color: #d46231;
}


.input-select:focus {
  border-color: $primary-color;
}

.input-select {
  box-sizing: border-box;
  display: block;
  height: 46px;
  width: 100px;
  margin-right: 16px;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 2;
  color: rgba(0, 0, 0, 0.87);
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.14);
  border-radius: .25rem;
  transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  outline: none;
  caret-color: #d46231;
}

.standard-button:hover {
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  background-color: darken($primary-color, 5);
  transition: .2s;
}

.standard-button:active {
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
  background-color: darken($primary-color, 10);
  transition: .1s;
}

.standard-button {
  background: $primary-color;
  color: white;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  border-radius: 50px;
  padding: 15px 45px;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  line-height: 1.5;
  outline: none;
  cursor: pointer;
  border: none;
  transition: .3s;

}

.standard-button[disabled] {
  background-color: #d5d5d5 !important;
  color: rgba(0, 0, 0, 0.38);
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12) !important;
  cursor: default;
}

.display-block {
  display: block;
}


.display-none {
  display: none;
}
