@import "../../configs/variables";

.showBackground {
  opacity: 1 !important;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);
  background-color: $primary-dark;
}

#ToolbarCmpnt {
  $height: 55px;
  transition: background-color .2s;
  box-sizing: border-box;
  position: fixed;
  width: 100%;
  z-index: 10;
  height: $height;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 0 $side-space;

  #menuIconButton{
    display: none;
    width: 50px;
    padding: 0;
    background-color: transparent;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 60px;
    height: 50px;
    margin-right: 16px;
    outline: none;

    &:Active{
      background-color: black;
      opacity: .2;
    }
  }

  #menuIcon {

    min-width: 34px;
  }

  .navOptionActive {
    font-weight: bold;
    //text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.25);
    border-bottom: white solid 4px;
    border-radius: 5px;
    margin-bottom: -5px;
  }

  .navOption:last-child {
    margin-right: 0;
  }

  .navOption {

    margin: 0 16px;
    padding: 0 8px;
    display: block;

    h3 {
      font-weight: lighter;
      color: white;
      margin: 0;
      cursor: pointer;
    }
  }

  #name {
    font-size: 28px;
    margin: 0;
    color: white;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.25);
  }

  #toolbarContainer {

    font-size: 20px;
    color: white;
    background-color: transparent;
    z-index: 5;
    position: relative;
    height: 100%;
    align-items: center;
  }

  .background {
    background-color: white;
    opacity: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
  }

  .transition {
    $transition-time: .25s;
    -webkit-transition: all $transition-time ease-in-out;
    -moz-transition: all $transition-time ease-in-out;
    -o-transition: all $transition-time ease-in-out;
    transition: all $transition-time ease-in-out;
  }


  .toolbarButtonText {
    font-size: 16px;
    font-weight: 400;
    color: white;
  }

  // ========== MEDIA QUERIES ==========================

  @media (max-width: 1000px) {
    #name {
      font-size: 24px;
    }
    .navOption {
      padding: 0;

    }
  }
  @media (max-width: 800px) {
    #menuIconButton{
      display: flex;
    }

    .navOption {
      display: none;

    }

  }

  @media (max-width: 600px) {
    padding: 0 $side-space-600;


  }


  @media (max-width: 450px) {

    #name {
      font-size: 20px;
    }

    .toolbarButtonText {
      font-size: 12px;
    }


    .logo {
      height: 40px;
    }

  }

}