@import "../../configs/variables";

#ContactUs {

  padding: 32px $side-space;
  background-color: #f3f3f3;
  border-top: 2px solid $secondary-color;
  min-height: 80vh;

  .form {
    margin: 0 auto;
    max-width: 700px;
    width: 90%;
  }

  .inputLabel {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 22px;

    label {
      width: 170px;
      text-align: right;
      margin-right: 16px;
      font-size: 20px;
      color: rgba(0, 0, 0, 0.58);
      height: 23px;
    }

    label[required]:after {
      color: $error-color;
      content: " *";
    }
  }

  .formError {
    color: $error-color;
    position: absolute;
    font-size: 12px;
    padding-left: 14px;
  }

  .inputHint {
    font-size: 12px;
    color: rgba(0, 0, 0, 0.86);
    padding-left: 14px;
    position: absolute;
  }

  #submitButton {
    width: calc(100% - 170px);
    display: block;
    margin: 16px 0 16px auto;
  }

  @media (max-width: 600px) {
    padding: 32px $side-space-600;


    .inputLabel {
      flex-direction: column;
      width: 100%;


      .flex-direction-column {
        width: 100%;
      }

      .input {
        width: 100%;
        margin-right: 0;
      }

      label {
        text-align: left;
        width: 100%;
        margin: 0;
        padding-left: 25px;
      }

    }

    #submitButton {
      width: 100%;
    }


  }

}